<template>
    <div class="margin-20 subAccountManage">
        <!--查询条件表单-->
        <div class="cm-panel" style="padding-bottom:10px">
            <i-form :model="formData" ref="formData" :label-width="60">
                <Row>
                    <i-col :xs="24" :sm="8" :md="6">
                        <Form-item label="用户姓名" prop="name">
                            <i-input type="text" clearable v-model="formData.name" placeholder="请输入">
                            </i-input>
                        </Form-item>
                    </i-col>
                    <i-col :xs="24" :sm="8" :md="6">
                        <Form-item label="手机号" prop="telephone">
                            <i-input type="text" clearable v-model="formData.telephone" placeholder="请输入">
                            </i-input>
                        </Form-item>
                    </i-col>
                    <i-col :xs="24" :sm="8" :md="6">
                        <div class="text-center font0">
                            <i-button @click="fnReset('formData')">重置</i-button>
                            <i-button type="primary" @click="querySubAccountList">查询</i-button>
                        </div>
                    </i-col>
                </Row>
            </i-form>
        </div>
        <!--查询列表结果-->
        <div class="cm-panel">
            <div class="text-right margin-b-10">
                <i-button type="primary" @click="addSubAccount" custom-icon="iconfont icon-add">新增子账号</i-button>
            </div>
            <i-table class="table-static-list" border :columns="columns"
                     :data="tableListData"></i-table>
            <Row class="margin-t-10">
                <Page :total="pager.total" :page-size="formData.Page" :current="formData.current"
                      :page-size-opts="pager.Opts" @on-change="pageChange" @on-page-size-change="pageSizeChange"
                      size="small" show-sizer show-elevator></Page>
            </Row>
        </div>
        <!--新增  编辑模态框-->
        <Modal v-model="showAddSubAccountModel" :mask-closable="false" footer-hide width="374"
               :title="addSubAccountTitleModel"
               class="showAddSubAccountModel">
            <Form ref="editFormData" :model="editFormData" :rules="ruleValidate" :label-width="58"
                  style="margin-top:-10px;">
                <FormItem label="用户账号" v-if="editFlag">
                    <span class="spanline32">{{editFormData.subAccount}}</span>
                </FormItem>
                <FormItem label="用户姓名" prop="name">
                    <Input v-model="editFormData.name" placeholder="请输入"></Input>
                </FormItem>
                <FormItem label="性别" prop="sex">
                    <RadioGroup v-model="editFormData.sex">
                        <Radio label="1">男</Radio>
                        <Radio label="0">女</Radio>
                    </RadioGroup>
                </FormItem>
                <FormItem label="手机号码" prop="telephone">
                    <Input v-model="editFormData.telephone" placeholder="请输入"></Input>
                </FormItem>
                <FormItem label="密码" prop="password">
                    <Input type="password" v-model="editFormData.password" placeholder="请输入"></Input>
                </FormItem>
                <FormItem label="验证码" prop="verificationCode">
                    <div class="font0">
                        <Input v-model="editFormData.verificationCode" placeholder="请输入"
                               class="half-gutter-width"></Input>
                        <Button type="primary" :disabled="smsSendBtnDisabled"
                                class="half-gutter-width margin-l-10 radius4"
                                @click="sendVerificationCode">
                            <span v-if="smsSendTiming">已发送({{auth_time}}S)</span>
                            <span v-else>发送验证码</span>
                        </Button>
                    </div>
                </FormItem>
                <div style="padding-left:58px;" v-if="sendSmsSuccessFlag">
                    <span class="sms-send-tip">验证码已发送至尾号{{phoneTailNumber}}的手机上，请填写该手机收到的验证码</span>
                </div>
            </Form>
            <div class="text-right margin-t-30 font0">
                <Button @click="handleReset('editFormData')">取 消</Button>
                <Button type="primary" :loading="confirmLoading" @click="handleSubmit('editFormData')">
                    <span v-if="!confirmLoading">确 定</span>
                    <span v-else>请求中...</span>
                </Button>
            </div>
        </Modal>
        <!--新增确定返回信息弹窗-->
        <Modal title="提示信息" v-model="showResModal" :mask-closable="false" footer-hide width="374" :closable="false">
            <Form :label-width="58" class="form-readonly">
                <!--<FormItem label="用户账号">-->
                <!--<span class="spanline32">{{infoSave.account}}</span>-->
                <!--</FormItem>-->
                <FormItem label="子账号">
                    <span class="spanline32">{{infoSave.subAccount}}</span>
                </FormItem>
                <FormItem label="用户姓名">
                    <span class="spanline32">{{infoSave.name}}</span>
                </FormItem>
                <FormItem label="手机号">
                    <span class="spanline32">{{infoSave.telephone}}</span>
                </FormItem>
                <FormItem label="账户状态">
                    <span class="spanline32">正常</span>
                </FormItem>
                <FormItem label="性别">
                    <span class="spanline32">{{infoSave.sex === '1'?'男':infoSave.sex === '0'?'女':'无'}}</span>
                </FormItem>
            </Form>
            <div class="text-right margin-t-20">
                <i-button type="primary" :loading="confirmLoading" @click="saveToList">确 定</i-button>
            </div>
        </Modal>
        <div class="simple-breadcrumb col-xs-11">
            <span>销售管理</span>
            <Icon type="ios-arrow-forward"></Icon>
            <span>子账号管理</span>
        </div>
    </div>
</template>

<script>
    import {mapMutations} from 'vuex'
    import {yx} from "@/utils/helper";
    import {
        validators,
        required,
    } from "@/utils/validateRules";


    export default {
        name: "subAccount",
        data() {
            return {
                account: this.$store.state.app.userInfo.userAccount,
                //查询条件数据
                formData: {
                    account: this.$store.state.app.userInfo.userAccount,//用户账号
                    telephone: '',//手机号
                    name: '',//用户姓名
                    current: 1,
                    rowCount: 10,
                },
                tableListData: [],//列表数据
                loading: false, //列表表格加载中状态
                pager: {
                    "total": 0,
                    "Opts": [10, 20, 30, 50,]
                },//表格分页数据
                columns: [{
                    title: '序号',
                    width: 66,
                    align: 'left',
                    fixed: 'left',
                    render: (h, params) => {
                        return h('span', params.index + (this.formData.current - 1) * this.formData.rowCount + 1);
                    }
                },
                    {
                        title: '操作',
                        width: 120,
                        key: 'action',
                        align: 'left',
                        fixed: 'left',
                        render: (h, params) => {
                            var array = []
                            //分隔符样式
                            var splitSpan = h('span', {
                                style: {
                                    'margin': '0 10px',
                                    color: '#D8D8D8 '
                                },
                            }, '|')
                            //0为可用，1为不可用
                            var txt = params.row.state === '1' ? '启用' : params.row.state === '0' ? '禁用' : '无'
                            var statusBtn = h("a", {
                                    on: {
                                        click: () => {
                                            this.switchStatus(params.row);
                                        }
                                    }
                                },
                                txt
                            )
                            var editBtn = h("a", {
                                on: {
                                    click: () => {
                                        this.edit(params.row)
                                    }
                                }
                            }, "修改")
                            array.push(statusBtn, splitSpan, editBtn);
                            return h("div", array);
                        }
                    },
                    {
                        title: '用户账号',
                        key: 'subAccount',
                    },
                    {
                        title: '用户姓名',
                        key: 'name',
                    },
                    {
                        title: '手机号',
                        key: 'telephone',
                    },
                    {
                        title: '状态',
                        key: 'state',
                        render: (h, params) => {
                            var row = params.row;
                            //0为可用，1为不可用
                            var txt = row.state === '0' ? '正常' : row.state === '1' ? '禁用' : '无';
                            return h("span", txt);
                        }
                    }
                ],
                editFlag: false,//新增还是修改
                showAddSubAccountModel: false,//显示新增/修改弹窗
                addSubAccountTitleModel: '',//模态框标题

                phoneTailNumber: '', //发送验证码的手机尾号
                smsSendTiming: false,//正在发送验证码倒计时
                sendSmsSuccessFlag: false,//验证码发送是否成功，成功后显示提示语
                auth_time: 0,//验证码已发送时间
                confirmLoading: false,//编辑弹窗中的确定按钮请求加载中
                showResModal: false,//显示新增后回显的确定弹窗
                infoSave: {},//新增后回显的确认数据obj

                //编辑表单数据绑定
                editFormData: {
                    account: this.$store.state.app.userInfo.userAccount,//用户账号
                    subAccount: "",//子用户帐号
                    name: "",//姓名
                    password: "",//密码
                    telephone: "",//电话号码
                    verificationCode: "",//验证码
                    sex: "",//性别   0 男 1 女
                    // state:'0',//状态 默认0 启用  1禁用  后台传
                },
                //编辑表单的校验
                ruleValidate: {
                    password: [{
                        validator: (rule, value, callback) => {
                            if (yx.isEmpty(value)) {
                                if (this.editFlag === true) {//修改时可以为空
                                    callback();
                                } else {
                                    callback(new Error('密码不能为空'))
                                }
                            } else { //不设校验条件，由后台返回
                                callback();
                            }
                        },
                        trigger: 'change'
                    }],
                    name: [{
                        required: true,
                        message: '用户姓名不能为空',
                        trigger: 'change'
                    },],
                    telephone: [required('手机号'), {
                        validator: validators.mobile,
                        trigger: 'change'
                    }],
                    sex: [
                        {required: true, message: '请选择性别', trigger: 'change'}
                    ],
                    verificationCode: [{
                        required: true,
                        message: '验证码不能为空',
                        trigger: 'change'
                    }],
                }
            }
        },
        computed: {
            //禁用发送验证码按钮
            smsSendBtnDisabled() {
                return this.smsSendTiming || this.confirmLoading || !/^1[3|4|5|6|7|8|9]\d{9}$/.test(this.editFormData.telephone)
            }
        },
        methods: {
            ...mapMutations('app', [
                'showLoading',
                'hideLoading',
            ]),
            errorModal(content, onOk, okText) {
                var arr = content.split(';');
                var text = '';
                arr.forEach(item => {
                    if (item) text += !!text ? ('；<br>' + item) : item;
                })
                var options = {
                    title: '提示',
                    okText: okText || '确 定',
                    content: text,
                };
                if (onOk) options.onOk = onOk;
                var t = setTimeout(() => {
                    this.$Modal.error(options);
                    clearTimeout(t);
                }, 300)

            },
            //清除表单所有数据
            fnReset(name) {
                this.$refs[name].resetFields();
                this.queryList();
            },
            //查询
            querySubAccountList() {
                var params = Object.assign({}, this.formData);
                this.formData.current = 1;
                this.queryList();
            },
            //新增信息确认按钮
            saveToList() {
                this.confirmLoading = true;
                this.showResModal = false;
                let t = setTimeout(() => {
                    this.confirmLoading = false;
                    clearTimeout(t);
                }, 1000)
                this.queryList();
            },
            //点击新增按钮
            addSubAccount() {
                this.$refs['editFormData'].resetFields();
                this.addSubAccountTitleModel = '新增子账号';
                this.showAddSubAccountModel = true;
                this.sendSmsSuccessFlag = false;
                this.auth_time = 0;
                this.editFlag = false;//新增
            },
            //修改
            edit(row) {
                this.$refs['editFormData'].resetFields();
                this.addSubAccountTitleModel = '修改账号信息';
                this.showAddSubAccountModel = true;
                this.sendSmsSuccessFlag = false;
                this.auth_time = 0;
                this.editFlag = true;//修改
                this.editFormData = Object.assign({}, row);//赋值表单
            },
            //表格翻页
            pageChange(curPage) {
                this.formData.current = curPage;
                this.queryList();
            },
            //表格改修改每页显示数量
            pageSizeChange(rowCount) {
                this.formData.current = 1;
                this.formData.rowCount = rowCount;
                this.queryList();
            },
            //新增/修改  确定
            handleSubmit(name) {
                this.$refs[name].validate((valid) => {
                    if (!valid) return false;
                    this.confirmLoading = true;
                    if (this.editFlag) {//修改
                        let params = Object.assign({}, this.editFormData);
                        params.account = this.account;
                        this.showLoading();
                        this.$http.post(polarisserver + '/subaccount/update', params).then((res) => {
                            this.hideLoading();
                            this.confirmLoading = false;
                            let result = res.data;
                            if (!result.success) return this.errorModal(result.errorMessage || '修改子帐号请求失败');
                            this.$Message.success('修改成功');
                            this.showAddSubAccountModel = false;
                            this.queryList();
                        }).catch((err) => {
                            this.hideLoading();
                            this.confirmLoading = false;
                            this.errorCatchModal('修改子账号请求出错：', err);
                        })
                    } else {//新增
                        let params = Object.assign({}, this.editFormData);
                        params.account = this.account;
                        this.showLoading();
                        this.$http.post(polarisserver + '/subaccount/add', params).then((res) => {
                            this.hideLoading();
                            this.confirmLoading = false;
                            let result = res.data;
                            if (!result.success) return this.errorModal(result.errorMessage || '新增子帐号请求失败');
                            this.$Message.success('新增成功');
                            this.showAddSubAccountModel = false;
                            this.showResModal = true;
                            this.infoSave = result.data || {};
                        }).catch((err) => {
                            this.hideLoading();
                            this.confirmLoading = false;
                            this.errorCatchModal('新增子账号请求出错：', err);
                        })
                    }
                })
            },
            //重置
            handleReset(name) {
                this.showAddSubAccountModel = false;
                this.$refs[name].resetFields();
            },
            //切换启用/禁用状态
            switchStatus(row) {
                var list = {
                    account: this.account,
                    subAccount: row.subAccount,
                    state: row.state === '1' ? '0' : '1',//启用或禁用的目标值
                }
                var tip = "", txt = "";
                //0为可用，1为不可用
                if (row.state === "1") {
                    tip = "<p>启用后，" + row.subAccount + "账号将开启进件，是否确认开启？</p>";
                    txt = "启用";
                } else {
                    tip = "<p>禁用后，" + row.subAccount + "账号将不能登录，确认是否禁用？</p>";
                    txt = "禁用";
                }
                //禁用
                this.$Modal.confirm({
                    title: "提 示",
                    content: tip,
                    onOk: () => {
                        this.showLoading();
                        this.$http.post(polarisserver + '/subaccount/disable', list).then((res) => {
                            this.hideLoading();
                            var result = res.data;
                            if (!result.success) return this.errorModal(result.errorMessage || '帐号' + txt + '请求失败');
                            this.queryList();
                        }).catch((err) => {
                            this.hideLoading();
                            this.errorCatchModal('帐号' + txt + '请求出错：', err);
                        })
                    },
                });
            },
            //获取列表
            queryList() {
                if (this.loading) return false;
                this.tableListData = [];
                this.loading = true;
                this.showLoading();
                let params = Object.assign({}, this.formData);
                this.$http.post(polarisserver + '/subaccount/query',
                    params).then((res) => {
                    this.hideLoading();
                    this.loading = false;
                    let result = res.data;
                    if (!result.success) return this.errorModal(result.errorMessage || '帐号列表查询请求失败');
                    let rowData = result.data || {};
                    this.pager.total = rowData.total;
                    this.tableListData = rowData.data || [];
                }).catch((err) => {
                    this.hideLoading();
                    this.loading = false;
                    this.errorCatchModal('帐号列表查询请求出错：', err);
                })
            },
            //发送验证码
            sendVerificationCode() {
                this.phoneTailNumber = "";
                this.auth_time = 60;//60读秒
                this.smsSendTiming = true;
                var auth_timetimer = setInterval(() => {
                    this.auth_time--;
                    if (this.auth_time <= 0) {
                        this.smsSendTiming = false;
                        clearInterval(auth_timetimer);
                    }
                }, 1000);
                var params = {
                    telephone: this.editFormData.telephone,
                    extraMap: {account: this.account}
                };
                this.$http.post(polarisserver + '/subaccount/sendShortMessage', params).then((res) => {
                    var result = res.data;
                    if (!result.success) return this.errorModal(result.errorMessage || '发送验证码请求失败');
                    this.phoneTailNumber = this.editFormData.telephone.substr(7);//取尾4位
                    this.sendSmsSuccessFlag = true;
                }).catch((err) => {
                    this.errorCatchModal('发送验证码请求出错：', err);
                })
            },
        },
        mounted() {
            document.title = '子帐号管理';
            this.queryList();//初始化查询表格
        }
    }
</script>

<style lang="less" scoped>
    .sms-send-tip {
        font-size: 12px;
        color: #666666;
        display: inline-block;
        line-height: 18px;
    }

    .ivu-btn.radius4 {
        border-radius: 4px;
    }
</style>



